*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  width: 100%;
  position: relative;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

input {
  padding: 0;
  margin: 0;
  border: 0;
  border: none;
}

button {
  padding: 0;
  border: none;
  cursor: pointer;
}

.containers {
  max-width: 1440px;
  padding: 0 120px;
  margin: 0 auto;
  width: 100%;
}

ol,
ul {
  padding-left: 0;
}

dl,
ol,
ul {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
}

.display-n {
  display: none !important;
}

.question-hide {
  transition: all 0.5s ease;
  height: 0;
  overflow: hidden;
  margin: 0 0 0 0 !important;
}

.ind-header {
  width: 100%;
  background: transparent;
  padding: 35px 0;
  position: relative;
  z-index: 5;
}

.ind-header__containers {
  max-width: 1440px;
  padding: 0 120px 0 100px;
  margin: 0 auto;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.ind-header__logo {
  cursor: pointer;
}

.ind-header__list {
  display: flex;
  align-content: center;
  justify-content: center;
  gap: 32px;
}

.ind-header__items {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 32px;
  cursor: pointer;
}

.ind-header__items-i {
  font-family: TT Norms;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120%;
  letter-spacing: 0.02em;
  color: #000000;
}

.ind-header__btns {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 31px;
}

.ind-header__start {
  padding: 15px 50px;
  border: 1.5px solid #000000;
  display: flex;
  align-items: center;
  cursor: pointer;
  justify-content: center;
  text-align: center;
  border-radius: 40px;
  background: transparent;
}

.ind-header__start p {
  font-family: TT Norms;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #000000;
}

.ind-header__enter {
  background: transparent;
  cursor: pointer;
}

.ind-header__enter p {
  font-family: TT Norms;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #000000;
}

.ind-footer {
  background: #4f6bfd;
  width: 100%;
  padding: 38px 0 57px 0;
}

.ind-footer__content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.ind-footer__content-logo {
  cursor: pointer;
  margin: 0 auto;
}

.ind-footer__content-logo a {
  margin: 0 auto;
}

.ind-footer__content-t {
  width: 788px;
  font-family: TT Norms;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  color: #ffffff;
}

.ind-footer__content-m {
  margin: 25px 0 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
}

.ind-footer__content-c {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border: 2px solid #ffffff;
  border-radius: 30px;
}

.ind-footer__content-c a {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ind-footer__content-l {
  margin: 60px 0 0 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.ind-footer__content-year {
  font-family: TT Norms;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #ededed;
}

.ind-footer__content-create {
  font-family: TT Norms;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #ededed;
  cursor: pointer;
}

.ind-footer__content-create a {
  font-family: TT Norms;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #ededed;
  cursor: pointer;
}

.ind-footer__content-path {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
  margin: 0 0 0 120px;
}

.ind-footer__content-path a {
  font-family: TT Norms;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120%;
  letter-spacing: 0.02em;
  color: #ffffff;
  cursor: pointer;
}

.ind-footer__content-path p {
  color: #fff;
  font-size: 16px;
}

.ind-headers {
  width: 100%;
  background: #fff3f0;
  position: relative;
  overflow: hidden;
}

.ind-headers::after {
  content: "";
  position: absolute;
  width: 577px;
  height: 577px;
  background: #ffc178;
  opacity: 0.8;
  border-radius: 50%;
  top: 50%;
  left: -300px;
  transform: translate(0%, -50%);
  filter: blur(300px);
  z-index: 2;
}

.ind-headers__wrapper {
  position: relative;
  padding: 25px 0 120px 0;
  z-index: 5;
  display: flex;
  align-items: flex-start;
  width: 100%;
  justify-content: space-between;
}

.ind-headers__wrapper::after {
  content: "";
  position: absolute;
  width: 25px;
  height: 25px;
  top: 0;
  left: 0;
  background: url("./../svg/headers/decor-1.svg") no-repeat;
}

.ind-headers__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 77px 0 0 0;
}

.ind-headers__content-t {
  font-family: NEXTART;
  width: 510px;
