  font-style: normal;
  font-weight: normal;
  font-size: 30px;
  line-height: 100%;
  letter-spacing: -0.03em;
  color: #000000;
  transition: all 0.5s ease;
}

.ind-tariff__content-p p span {
  font-family: TT Norms;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 100%;
  letter-spacing: -0.03em;
  color: #000000;
  transition: all 0.5s ease;
  margin: 0 10px;
}

.ind-tariff__content-plus {
  position: relative;
  margin: 20px 0 0 0;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 20px;
}

.ind-tariff__content-plus:last-child {
  margin: 40px 0 0 0;
}

.ind-tariff__content-plus:nth-child(3)::after {
  content: "";
  position: absolute;
  top: 92px;
  left: 32px;
  width: 230px;
  height: 47px;
  background: url("./../svg/tariff/decor-4.svg") no-repeat;
}

.ind-tariff__content-plus p {
  font-family: TT Norms;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 150%;
  letter-spacing: -0.011em;
  color: #525666;
  transition: all 0.5s ease;
}

.ind-tariff__content-btn {
  background: #4f6bfd;
  transition: all 0.5s ease;
  border-radius: 100px;
  padding: 16px 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 13px;
  cursor: pointer;
  margin: 35px 0 0 0;
}

.ind-tariff__content-btn p {
  font-family: TT Norms;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 120%;
  letter-spacing: 0.02em;
  color: #e5f6ff;
  transition: all 0.5s ease;
}

.ind-banner {
  padding: 81px 0 130px 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #fff;
}

.ind-banner__content {
  width: 100%;
  padding: 60px 33px;
  background: #4f6bfd;
  border: 2px solid #000000;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  border-radius: 38px;
}

.ind-banner__content-t {
  font-family: NEXTART;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 45px;
  text-align: center;
  color: #ffffff;
  position: relative;
}

.ind-banner__content-t::after {
  content: "";
  position: absolute;
  top: 40px;
  left: 50%;
  transform: translate(-50%, 0%);
  width: 474px;
  height: 57px;
  background: url("./../svg/banner/decor-1.svg") no-repeat;
}

.ind-banner__content-d {
  margin: 40px auto 0 auto;
  font-family: TT Norms;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 150%;
  letter-spacing: -0.011em;
  color: #ffffff;
}

.ind-banner__content-b {
  margin: 47px auto 0 auto;
  padding: 16px 40px;
  background: #ffffff;
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 13px;
  cursor: pointer;
}

.ind-banner__content-b p {
  font-family: TT Norms;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 120%;
  letter-spacing: 0.02em;
  color: #4f6bfd;
}

.ind-question {
  background: #fcf7f7;
  width: 100%;
  padding: 108px 0 135px 0;
  overflow: hidden;
}

.ind-question__content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
}

.ind-question__content::after {
  content: "";
  position: absolute;
  top: -65px;
  left: 0;
  width: 25px;
  height: 25px;
  background: url("./../svg/question/decor-1.svg") no-repeat;
}

.ind-question__content-t {
  font-family: NEXTART;
  font-style: normal;
  font-weight: bold;
  font-size: 35px;
  line-height: 52px;
  color: #333333;
}

.ind-question__content-d {
  position: absolute;
  top: 239px;
  left: -266px;
}

.ind-question__content-l {
  margin: 40px 0 0 470px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 40px;
}

.ind-question__content-i {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 730px;
}

.ind-question__content-c {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.ind-question__content-c h3 {
  width: 95%;
  font-family: TT Norms;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 130%;
  letter-spacing: -0.02em;
}

.ind-question__content-c img {
  transition: all 0.5s ease;
}

.ind-question__content-line {
  margin: 28px 0 20px 0;
  width: 730px;
  height: 0px;
  border-top: 1px solid #525666;
}

.ind-question__content-text {
  margin: 60px 0 10px 0;
  font-family: TT Norms;
  font-style: normal;
  font-weight: 500;
  transition: all 0.5s ease;
  font-size: 20px;
  line-height: 150%;
  letter-spacing: -0.011em;
  color: #525666;
  width: 90%;
}